<template >
  <div @click.self="hideMe($event)" id="bg">
    <div id="inputContainer">
      <i class="fas fa-external-link-alt fa-2x ico"></i>
      <input v-model="url" type="text" placeholder="https.//www.BildURL.de/EinBild.jpg"/>
      <Button v-on:click.native="sendURL($event)" Title="Bild kopieren"/>

      <div id="close" @click="hideMe($event)" ><i class="fa fa-times ico" aria-hidden="true"></i></div>
    </div>
  </div>    
</template>

<script>
import Button from '@/components/Button.vue'

export default {
  name: 'Modal',
  components:{
    Button,
  },
  data() {
    return{
      url:'',
    }
  },
  methods:{
    sendURL(event){
      event.preventDefault();
      this.$emit('send-url', this.url)      
    },
    hideMe(event){
      event.preventDefault();
      this.$emit('hideModalURL');
    }
  }
}
</script>

<style lang="scss" scoped>
.ico{
  color: $prime;
}
#close{
  cursor: pointer;
  display: flex;
  align-self: flex-start;
  font-size: 15pt;
  margin:5px;
}
input{
  border:none;
  padding-left:15px;
  padding-right:15px;

  border-bottom: solid 2px $prime;

  background:rgba($color: $prime, $alpha: 0.1);
  height:50px;
  width: 370px;
  border-radius: 10px;
  font-size: 11pt;
  color: $second;
  font-weight:700;
  letter-spacing: 1px;
}

#inputContainer{
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 650px;
  height: 200px;
  background-color: #FFFFFF;
  border-radius: 25px;
  padding-left: 15px;
  padding-right:15px;
  justify-content: space-between;
}

#bg{
  position: absolute;
  width: 100vw;
  height: 100vh;
  background: rgba($color: #000000, $alpha: 0.3);
  display: flex;
  flex-direction: column;
  margin: 0px;
  top: 0px;
  left: 0px;
  justify-content: center;
  align-items: center;
}
</style>
