<template>
  <div :style="style">
      {{value}}
  </div>   
</template>

<script>
export default {
    name:'tag',
    computed: {
      style () {
        return 'background-color: ' + this.color;
      }
    },
    props: {
        value: {
            type: String,
            default: '',
        },
        color:{
            type: String,
            default: '#eeeeee',
        }
    },
};
</script>

<style lang="scss" scoped>

div{
    min-width: 100px;
    height: 30px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

</style>